import React from 'react'
import { Layout, Stack, Main, Sidebar } from '@layout'
import PageTitle from '@components/PageTitle'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import ContactForm from '@widgets/ContactForm'
import ContactInfo from '@widgets/ContactInfo'
import Commitment from '@widgets/Commitment'

const About = props => (
  <Layout {...props}>
    <Seo title='About Us' />
    <Divider />
    <Stack>
      <Main>
        <PageTitle
          header="About Us"
          subheader='The train sim depot was started in 2020 by Gareth Davies originally as a YouTube channel providing tutorials on various topics in TS2021. Since then Gareth has developed this brand new website for news and other updates regarding the railway and TS.

          '
        />
        <Divider />
        <h1>TRAIN SIMULATOR NEWS</h1>
<p>
We provide the latest train simulator news straight from DTG and third parties. We mainly focus on third parties and what's going on in the community. We are slowly but surely building a reputation as the place to go for updated TS News.
   </p>

   <h1>Railway News</h1>    
   <p>
We have recently been getting into the general railway news and not just Train Simulator. We have dedicated authors who provide quality railway news.</p>

      </Main>
      <Sidebar>
        <Commitment />
        <Divider />
        <ContactInfo />
      </Sidebar>
    </Stack>
  </Layout>
)

export default About
